* {
	box-sizing: border-box;
}

.body {
	background-image: linear-gradient(to right, black, rgb(71, 1, 1), black);
	background-repeat: round;
	color: rgb(153, 153, 153);
	font-family: 'American Typewriter', serif;
	text-shadow: 2px 2px rgb(0, 0, 0, 0.5);
	height: 100%;
	min-height: 100%;
	line-height: 1;
}
